        handleSelect(key, keyPath) {
            if(key == 1){
                //
                this.$router.push({path:'/addque'});
            }
            else if(key == 2){
                //
                let that = this;
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                    }).then(() => {
                        that.saveChoose()
                        that.$router.push({path:'/choose'})
                    //save
                    }).catch(() => { 
                        that.$router.push({path:'/choose'})
                });
            }else if(key == 3){
                
            }
